import { useEffect } from "react";
import { dispatch } from "reshow";
import { navigationDispatch } from "organism-react-navigation";
import { ajaxDispatch } from "organism-react-ajax";
var init;

var usePage = function usePage(props) {
  var pageName = props.pageName,
      tplProps = props.tplProps;
  useEffect(function () {
    setTimeout(function () {
      navigationDispatch({
        params: {
          activeMenu: pageName
        }
      });
      dispatch({
        tplProps: tplProps,
        pageName: pageName
      });
    });

    if (!init) {
      init = 1;
      ajaxDispatch("ajaxGet", {
        url: "/data/env",
        ini: true
      });
    }

    return function () {
      navigationDispatch({
        params: {
          activeMenu: null
        }
      });
    };
  }, []);
};

export default usePage;